@import "https://fonts.googleapis.com/css2?family=Cormorant+Garamond:wght@300;400&family=Montserrat:wght@200;400;600;800&display=swap";
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: Montserrat, sans-serif;
  overflow-x: hidden;
}

div#main {
  width: 100vw;
  flex-direction: column;
  display: flex;
}

section {
  width: 100vw;
  position: relative;
}

section.min-height {
  min-height: 100vh;
}

section.padding {
  padding: 5rem 0;
}

section img.background {
  height: 100%;
  object-fit: cover;
  pointer-events: none;
  width: 100vmax;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translate(-50%);
}

h1 {
  margin: 0;
  font-family: Cormorant Garamond, serif;
  font-size: 7rem;
  font-weight: 400;
  line-height: .8;
}

h2 {
  margin: 0;
  padding-top: 1rem;
  font-size: 2rem;
  font-weight: 400;
}

h3 {
  margin: 0;
  font-size: 1.25rem;
  font-weight: 400;
}

p {
  margin-bottom: 0;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.5;
}

p.large {
  font-size: 24px;
}

p.small {
  font-size: 15px;
}

hr {
  margin: 2.5rem;
}

li {
  line-height: 1.7;
}

li::marker {
  color: #4e567e;
}

a {
  color: #000;
  text-decoration: none;
}

a:hover {
  color: #4e567e;
}

.container {
  grid-gap: 24px;
  max-width: 1200px;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  margin: 20px auto;
  padding: 0 20px;
  display: grid;
}

.container .box {
  text-align: center;
  backdrop-filter: blur(8px);
  background: #ffffff0f;
  border: 1px solid #22d3ee40;
  border-radius: 16px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 28px 20px;
  transition: transform .25s, box-shadow .25s, border-color .25s;
  display: flex;
  box-shadow: 0 10px 25px #7c3aed33;
}

img.socialIcon {
  height: 35px;
  width: 35px;
  filter: brightness(0) invert();
  transition: transform .2s, filter .2s;
}

img.socialIcon:hover {
  transform: scale(1.1);
}

.home-container {
  width: 100%;
  height: 100vh;
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.home-container:before {
  display: none;
}

.background {
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  position: absolute;
}

.home-text {
  color: #fff;
  max-width: 90%;
  background: #0009;
  border-radius: 10px;
  padding: 20px;
}

.home-text h1 {
  margin-bottom: .5rem;
  font-size: 3rem;
  font-weight: bold;
}

.home-text h2 {
  font-size: 1.5rem;
  font-weight: lighter;
}

.home-arrow {
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

.home-arrow img {
  height: 3rem;
  width: 3rem;
  animation: bounce 2s infinite;
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-10px);
  }
}

.blog-card {
  transition: all .3s;
}

.blog-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 25px #00000026;
}

a:focus {
  outline-offset: 2px;
  outline: 2px solid #4e567e;
}

@media (max-width: 768px) {
  .container {
    grid-template-columns: 1fr;
    margin: 20px;
  }

  .home-text h1 {
    font-size: 2rem;
  }

  .home-text h2 {
    font-size: 1.2rem;
  }

  section.padding {
    padding: 3rem 0;
  }
}

html {
  scroll-behavior: smooth;
}

.box {
  transition: transform .2s;
}

.box:hover {
  transform: translateY(-2px);
}

a {
  transition: color .2s;
}

.floating-whatsapp {
  width: 70px;
  height: 70px;
  color: #fff;
  z-index: 999;
  cursor: pointer;
  background: linear-gradient(135deg, #25d366 0%, #128c7e 100%);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all .3s cubic-bezier(.34, 1.56, .64, 1);
  display: flex;
  position: fixed;
  bottom: 30px;
  right: 30px;
  box-shadow: 0 8px 24px #25d36659;
}

.floating-whatsapp:hover {
  background: linear-gradient(135deg, #20ba5a 0%, #0d6c5f 100%);
  transform: scale(1.15)translateY(-5px);
  box-shadow: 0 12px 32px #25d36680;
}

.floating-whatsapp:active {
  transform: scale(.95);
}

.floating-whatsapp svg {
  width: 40px;
  height: 40px;
  fill: #fff;
  filter: drop-shadow(0 2px 4px #0000001a);
}

@media (max-width: 768px) {
  .floating-whatsapp {
    width: 60px;
    height: 60px;
    bottom: 20px;
    right: 20px;
  }

  .floating-whatsapp svg {
    width: 35px;
    height: 35px;
  }
}

@media (max-width: 480px) {
  .floating-whatsapp {
    width: 55px;
    height: 55px;
    bottom: 15px;
    right: 15px;
  }

  .floating-whatsapp svg {
    width: 30px;
    height: 30px;
  }
}

